import React, { useEffect, useState } from 'react';
import '../../css/packages.css'
import PackageItem from './PackageItem'
import axios from 'axios';

let baseUrl = "/pac/"

let loading = (
    <div className="loader-container">
      <div className="loader">
      
      </div>
    </div>
  )

function compare(x,y,reverse){
    if(reverse) return x > y;
    return x < y;
}

function sortByPrice(items,reverse){

    for(let i=0;i<items.length-1;i++){
        for(let j=1;j<items.length-i;j++){
            if(compare(items[j].price,items[j-1].price,reverse)){
                let temp = items[j];
                items[j] = items[j-1];
                items[j-1] = temp;
            }
        }
    }

    return items;
}

function sortByDuration(items,reverse){

    for(let i=0;i<items.length-1;i++){
        for(let j=1;j<items.length-i;j++){
            if(compare(items[j].night,items[j-1].night,reverse)){
                let temp = items[j];
                items[j] = items[j-1];
                items[j-1] = temp;
            }
        }
    }

    return items;
}


function changeTitle(title){
    document.title = title;
}

function PackagesWrapper(props) {

    const [packagesItems, setPackageItems] = useState(null);
    const [title, setTitle] = useState(null);

    useEffect(()=>{
        const packageId = props.match.params.packageId;
        let fullPath = baseUrl + packageId;

        changeTitle(`${packageId.toUpperCase()} Tour: ${packageId.toUpperCase()} Packages at Best Price`)

        setTitle(packageId);

        axios.get(fullPath).then((res)=>{
            setPackageItems(res.data);

            let filter =  document.querySelector('.sort')
            filter.addEventListener('change',(e)=>{
                if(e.target.value == "1" || e.target.value == "2"){
                    let reverse = e.target.value == "1" ? false : true;
                    let filteredItems = sortByPrice(res.data,reverse);
                    setPackageItems([]);
                    setPackageItems(filteredItems);
                }else if(e.target.value == "3" || e.target.value == "4"){
                    let reverse = e.target.value == "4" ? false : true;
                    let filteredItems = sortByDuration(res.data,reverse);
                    setPackageItems([]);
                    setPackageItems(filteredItems);
                }
            })

        })

    },[])


    let listItems = packagesItems != null ? packagesItems.length != 0 ? packagesItems.map((item,index)=>{
        return <PackageItem key={`${index}`} packagesId={title} data={item} />
    }) : (
        <div>
            <h4 className="center" style={{marginBottom: '24px'}}>No Packages Found</h4>
        </div>
    ) : (
        loading
    )
    
    return (
        <div className="container" id="all-packages">
            <div className="package-header row">
                <div className="col s12 m10">
                    <h4 style={{textTransform: 'capitalize'}}>{ title != null ? title : '' } Tour Packages</h4>
                    <h6>All Packages</h6>
                </div>
                <div className="col s12 m2" id="filter-container">
                    <select className="browser-default sort" defaultValue="0">
                        <option value="0" disabled>Sort By: Select</option>
                        <option value="1">Price- Low to High</option>
                        <option value="2">Price- High to Low</option>
                        <option value="3">Duration- High to Low</option>
                        <option value="4">Duration- Low to High</option>
                    </select>
                </div>
            </div>

            <div className="packages">
                { listItems }
            </div>

        </div>
    );
}

export default PackagesWrapper;
