import React, { Component } from 'react'

function getIconName(inclusion){
    switch(inclusion){
        case "Flights":
            return "flight_takeoff";
        case "Hotels":
            return "hotel";
        case "Meals":
            return "restaurant_menu";
        case "Sightseing":
            return "camera_alt"
        case "Bus":
            return "directions_bus"
        case "Transport":
            return "commute"        
        default:
            return "brightness_7"                
    }
}

class PackageItem extends Component{

    handleHelp = ()=>{
        window.open("https://api.whatsapp.com/send?phone=+919818040401","_blank")
    }

    render(){
        let { name, price, features, scheduleString, imageUrls, days, night, priceType } = this.props.data;
        let packagesId = this.props.packagesId;

        return (
            <div className="package-item row">
                <div className="col s12 m9 row package-data z-depth-1">
                    <div className="col s12 m4">
                        <img src={`/images/${imageUrls[0]}`} className="package-image" />
                    </div>
    
                    <div className="col s12 m8 package-item-content">
                        <div className="header">
                            <a href={`/package/${packagesId}/${name}`}><h6 style={{textTransform:'capitalize'}}>{ name }</h6></a>
                            <h6>{`${night} Nights & ${days} Days`}</h6>
                        </div>
    
                        <div className="path">
                            <p>Schedule: { scheduleString }</p>
                        </div>
    
                        <div style={{marginLeft:'8px'}}>
                            <h6>Inclusions</h6>
                            <div className="inclusions">
                                {
                                    features.map((feature,index)=>{
                                        return (
                                            <div className="inclusion" key={`${index}`}>
                                                <span><i className="material-icons">{ getIconName(feature) }</i></span>
                                                <p>{ feature }</p>
                                            </div>                                       
                                        )
                                    })
                                }
                            </div>
                        </div>
    
                    </div>
                </div>
    
                <div className="col s12 m3 tour-item-book z-depth-1">
                    <div className="data">
                        <h5>â‚¹ { price }</h5>
                        <p>Starting price { priceType }</p>
                        <div>
                            <button onClick={this.handleHelp} style={{backgroundColor:'var(--primaryColor)',color: 'white'}}>Need Help ?</button>
                            <a href={`/package/${packagesId}/${name}`}><button style={{backgroundColor:'var(--primaryColor)',color: 'white'}}>Know More</button></a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default PackageItem;